import { component } from 'picoapp';
import { str2json } from '../../utilities';
import { gsap } from 'gsap';

class CollectionLayout {
  constructor(el, ctx) {
    this.init(el);
    this.bindEvents();
  }

  init(el) {
    this.el = el;
    this.buttons = el.querySelectorAll('.Button');
    this.loop = document.querySelector('.Loop--products');
  }

  bindEvents() {
    this.buttons.forEach(btn => {
      btn.addEventListener('click', ev => {
        ev.preventDefault();
        if(btn.dataset.columns == 'spacious'){
          this.loop.setAttribute('data-layout', 'spacious');
          this.setActive(btn)
        } else if(btn.dataset.columns == 'dense'){
          this.loop.setAttribute('data-layout', 'dense')
          this.setActive(btn)
        }
      })
    });
  }
  setActive(activeBtn){
    activeBtn.classList.add('active');
    this.buttons.forEach(btn => {
      if(btn != activeBtn){
        btn.classList.remove('active');
      }
    })
  }
}

export default component((node, ctx) => {
  new CollectionLayout(node, ctx);
});